#toDoBox {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.7);
  border: 2px solid rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  margin-bottom: 10px;
}

#contentBox {
  display: flex;
  justify-content: center;
  align-items: center;
}

#content {
  font-weight: 400;
  margin-left: 10px;
}

#buttonBox {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

#done > span {
  color: green;
}

#delete > span {
  color: #c23616;
}

.done {
  opacity: 0.3;
  text-decoration: line-through;
}

button {
  width: 30px;
  height: 30px;
  background: transparent;
  border-radius: 50%;
  margin-right: 10px;
  cursor: pointer;
}

button:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
